.product-card {
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  padding: 32px 16px 16px;
  display: flex;
}

.product-card__img {
  width: 160px;
  height: 134px;
  margin: 0 auto;
}

.product-card__title {
  letter-spacing: 0;
  text-align: left;
  color: #060b35;
  margin: 40px 0 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.product-card__code {
  letter-spacing: 0;
  text-align: left;
  color: #616070;
  margin: 4px 0 0;
  font-size: 10px;
  line-height: 14px;
}

.product-card__rating {
  justify-content: space-between;
  align-items: center;
  margin: 16px 0 0;
  display: flex;
}

.product-card__reviews {
  color: #060b35;
  font-size: 10px;
  line-height: 14px;
}

.product-card__price-list {
  justify-content: space-between;
  align-items: center;
  margin: 24px 0 0;
  display: flex;
}

.product-card__item--price {
  text-align: left;
  color: #616070;
  font-size: 12px;
  line-height: 18px;
}

.product-card__item--number {
  text-align: right;
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.product-card__btn {
  color: #fff;
  text-transform: uppercase;
  text-align: center;
  background-color: #00acdc;
  border-radius: 5px;
  width: 100%;
  height: 40px;
  margin-top: 16px;
  padding: 12px 70px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  display: block;
}

.product-card__btn:hover {
  color: #00acdc;
  background: none;
  border: 1px solid #00acdc;
}

.stars {
  display: flex;
}

.stars__star {
  background: url("star.822d561e.svg") center no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__star:last-child {
  margin-right: 0;
}

.stars--5 .stars__star, .stars--1 .stars__star:nth-child(-n+1), .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4) {
  background: url("star-active.edda4c43.svg") center no-repeat;
}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: Roboto, sans-serif;
}

p {
  margin: 0;
}
